export default {

	mounted() {
		
		},
	computed: {
		totalmoney() {
			var totalNum = Number(this.total) + Number(this.express)

			return totalNum
		}
	},
	data() {
		return {
			buyList: [], //订单列表
			goodsinfo: {},
			number: "", //购买商品总数量
			goodsPrice: 0.0, //商品合计价格
			sumPrice: 0.0, //用户付款价格
			address: 9, //地址是否显示
			addrList: {}, //选中地址信息
			goods: [],
			express: '',
			total: "", //商品总价格
			iscart: "",
			getgoods_name: false, //送货类型显示
			order_no: "", //订单编号
			orderType: "", //地址类型
			defaultAddr: "", //默认地址
			addrId: "" //默认地址id
		}
	},
	onLoad(option) {

		console.log("con", option);
		if (option.iscart) {
			//立即购买商品缓存
			uni.getStorage({
				key: "cart",
				success: (res) => {
					console.log("liji", res)
					this.iscart = res.data.isCart;
					if (res.data.isCart == 0) {
						var quick = {
							goods_id: res.data.goods.id,
							goods_number: res.data.goods.goods_number,
							goods_spec: res.data.goods.goods_spec
						}

						this.goods.push(quick)

						//确认订单信息
						this.request({
							url: this.config.url + "order/sure",
							method: "post",
							data: {
								token: this.token,
								goods: this.goods
							},
							success: (res) => {
								console.log("sure", res)
								if (res.data.code == 1) {
									this.buyList = res.data.data.goods; //商品列表
									this.express = res.data.data.express;
									this.total = res.data.data.total;
									this.number = res.data.data.number;
									this.orderType = res.data.data.order_type; //提交记录

									//判断是否有默认地址
									if (this.orderType == 0) {
										this.address = false;
									} else if (this.orderType == 1) {
										this.getgoods_name =1;
										this.address = true;
										this.addrList = res.data.data.address //默认地址
										this.addrId = res.data.data.address.id; //默认地址id

									} else if (this.orderType == 2) {
										this.getgoods_name = 2;
										this.address = true;
										this.addrList = res.data.data.address //默认地址

										this.addrId = res.data.data.address.id; //默认地址id
									}
								}
							}
						})

					} else {

						//购物车购买商品缓存
						// var quick={goods_id:res.data.id,goods_number:res.data.goods_number,goods_spec:res.data.goods_spec}
						this.goods = res.data.goods;
						//确认订单信息
						this.request({
							url: this.config.url + "order/sure",
							method: "post",
							data: {
								token: this.token,
								goods: this.goods
							},
							success: (res) => {
								console.log("cart", res)
								if (res.data.code == 1) {
									this.buyList = res.data.data.goods; //商品列表
									this.express = res.data.data.express;
									this.total = res.data.data.total;
									this.number = res.data.data.number;
									this.orderType = res.data.data.order_type; //提交记录

									//判断是否有默认地址
									if (this.orderType == 0) {
										this.address = false;
									} else if (this.orderType == 1) {
										this.getgoods_name = 1;
										this.address = true;
										this.addrList = res.data.data.address //默认地址
										this.addrId = res.data.data.address.id; //默认地址id
									} else if (this.orderType == 2) {
										this.getgoods_name = 2;
										this.address = true;
										this.addrList = res.data.data.address //默认地址
										this.addrId = res.data.data.address.id; //默认地址id
									}
								}
							}
						})

					}



				},
			})

		}else{
			//获取收货地址缓存
			
			uni.getStorage({
				key: "address",
				success: (res) => {
			
					var addr = JSON.parse(res.data)
					console.log("address", res, addr)
					if (addr.distance) {
						this.addrId = addr.id;
						this.getgoods_name = 1
						this.addrList = addr;
					} else {
						this.addrId = addr.id;
						this.addrList = addr;
						this.getgoods_name = 2;
					}
			
				}
			});
			//获取商品缓存
			//立即购买商品缓存
			uni.getStorage({
				key: "cart",
				success: (res) => {
					console.log("liji", res)
					this.iscart = res.data.isCart;
					if (res.data.isCart == 0) {
						var quick = {
							goods_id: res.data.goods.id,
							goods_number: res.data.goods.goods_number,
							goods_spec: res.data.goods.goods_spec
						}
			
						this.goods.push(quick)
			
						//确认订单信息
						this.request({
							url: this.config.url + "order/sure",
							method: "post",
							data: {
								token: this.token,
								goods: this.goods
							},
							success: (res) => {
								console.log("sure", res)
								if (res.data.code == 1) {
									this.buyList = res.data.data.goods; //商品列表
									this.express = res.data.data.express;
									this.total = res.data.data.total;
									this.number = res.data.data.number;
									
								}
							}
						})
			
					} else {
			
						//购物车购买商品缓存
						// var quick={goods_id:res.data.id,goods_number:res.data.goods_number,goods_spec:res.data.goods_spec}
						this.goods = res.data.goods;
						//确认订单信息
						this.request({
							url: this.config.url + "order/sure",
							method: "post",
							data: {
								token: this.token,
								goods: this.goods
							},
							success: (res) => {
								console.log("cart", res)
								if (res.data.code == 1) {
									this.buyList = res.data.data.goods; //商品列表
									this.express = res.data.data.express;
									this.total = res.data.data.total;
									this.number = res.data.data.number;
								
			
								}
							}
						})
			
					}
			    },
			})
		}

	},

	filters: {
		toFixed: function(x) {
			return parseFloat(x).toFixed(2);
		}
	},
	methods: {
		//选取送货方式
		sendType() {
			uni.navigateTo({
				url: "/pages/sendType/sendType"
			})
		},


		clearOrder() {
			uni.removeStorage({
				key: 'buylist',
				success: (res) => {
					this.buylist = [];
					//console.log('remove buylist success');
				}
			});
		},
		toPay() {
			//商品列表
			this.request({
				url: this.config.url + "order/order",
				method: "post",
				data: {
					token: this.token,
					goods: this.goods,
					order_type: this.getgoods_name,
					address_id: this.addrId,
					from_car: this.iscart
				},
				success: (res) => {
					console.log(res)
					if (res.data.code == 1) {
						this.order_no = res.data.data.order_no; //获取订单编号

						//调起支付接口
						this.request({
							url: this.config.url + "order/pay",
							method: "POST",
							data: {
								token: this.token,
								order_no: this.order_no
							},
							success: (res) => {
								console.log(res)
								if (res.data.code == 1) {
									var pay = res.data.data.data
									uni.requestPayment({
										provider: 'wxpay',
										appid: pay.appId,
										timeStamp: pay.timeStamp,
										nonceStr: pay.nonceStr,
										package: pay.package,
										signType: pay.signType,
										paySign: pay.paySign,
										success: function(res) {
											console.log('success:' + JSON.stringify(res));
											uni.navigateTo({
												url: "/pages/user/order_list/order_list?tbIndex=" + 1
											})

										},
										fail: function(err) {
											console.log('fail:' + JSON.stringify(err));
											uni.switchTab({


												url: "/pages/tabBar/home"
											})
											uni.showToast({
												title: "支付失败"
											})
										}
									});
								}else{
									uni.showToast({
										title: res.data.info,
										duration: 1000
									})
								}
							}
						})
					} else if (res.data.code == 0) {
						uni.showToast({
							title: res.data.info,
							duration: 1000
						})
					}
				}
			})


		}

	}
}
